.about {
  padding: 4rem 2rem;
  background-color: #fdfdfd;
  color: #333;
  text-align: center;
}

.about-header {
  margin-bottom: 3rem;
}

.about-header h2 {
  font-size: 2.5rem;
  color: #2d6a4f;
}

.about-header p {
  font-size: 1.2rem;
  color: #555;
  margin: 1rem auto;
  max-width: 800px;
  line-height: 1.6;
}

/* Approach Section */
.approach-section {
  margin-top: 4rem;
}

.approach-section h3 {
  font-size: 2rem;
  color: #40916c;
  margin-bottom: 1rem;
}

.approach-description {
  font-size: 1.2rem;
  color: #666;
  max-width: 800px;
  margin: 0 auto 2rem auto;
  line-height: 1.6;
}

.about-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.about-card {
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: left;
}

.about-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2);
}

.about-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}

.about-card h4 {
  font-size: 1.5rem;
  color: #2d6a4f;
  margin: 1rem 0 0.5rem;
}

.about-card p {
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
}

/* Team Section */
.team-section {
  margin-top: 4rem;
}

.team-section h3 {
  font-size: 2rem;
  color: #40916c;
  margin-bottom: 2rem;
}

.team-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

.team-member {
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 300px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.team-member:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2);
}

.team-member img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 1rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.team-member h4 {
  font-size: 1.5rem;
  color: #2d6a4f;
  margin-bottom: 0.5rem;
}

.team-member p {
  font-size: 1rem;
  color: #555;
  margin: 0.5rem 0;
}

.team-member p:last-child {
  margin-top: 1rem;
}
