.navbar {
  background-color: #95d5b2;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative; /* Ensure dropdown is positioned relative to the navbar */
}

.navbar-logo img {
  height: 50px;
  width: auto;
}

.navbar-toggle {
  display: none;
  font-size: 2rem;
  cursor: pointer;
  color: #2d6a4f;
  z-index: 10; /* Ensure toggle stays above dropdown */
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
}

.navbar-links li a {
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  text-decoration: none;
  color: #2d6a4f;
}

.navbar-links li a:hover {
  background-color: #52b788;
  color: white;
}

/* Mobile View */
@media (max-width: 768px) {
  .navbar-toggle {
    display: block;
  }

  .navbar-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100%; /* Position the dropdown below the navbar */
    right: 1rem; /* Align dropdown to the right near the toggle button */
    background-color: #95d5b2;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 5px; /* Add rounded corners for better aesthetics */
    width: 200px; /* Set a fixed width for the dropdown */
    padding: 0.5rem 0; /* Add spacing inside the dropdown */
    z-index: 100; /* Ensure dropdown appears above other elements */
  }

  .navbar-links.open {
    display: flex;
  }

  .navbar-links li {
    text-align: center;
    margin: 0.5rem 0; /* Add spacing between dropdown items */
  }

  .navbar-links li a {
    padding: 0.5rem 1rem; /* Ensure consistent padding for dropdown links */
  }
}
