.menu {
  padding: 2rem;
  background-color: #f9f9f9;
  color: #333;
  text-align: center;
}

.menu-header h2 {
  font-size: 2.5rem;
  color: #2d6a4f;
  margin-bottom: 1rem;
}

.menu-header p {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 2rem;
}

.menu-section {
  margin-bottom: 3rem;
}

.menu-section h3 {
  font-size: 2rem;
  color: #40916c;
  margin-bottom: 1.5rem;
}

.menu-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  justify-items: center;
}

.menu-item {
  background-color: white;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: left;
  display: grid;
  grid-template-columns: 80px auto;
  gap: 1rem;
  align-items: center;
}

.menu-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2);
}

.menu-item img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 50%;
}

.menu-info span {
  display: block;
  font-weight: bold;
  color: #2d6a4f;
  margin-bottom: 0.25rem;
}

.menu-info span:last-child {
  color: #999;
  font-size: 0.9rem;
}

.menu-info p {
  font-size: 0.9rem;
  color: #555;
}
